<template>
  <user>
    <html-head 
      title="General Settings"
    ></html-head>

    <page-header>
      <template v-slot:title >
        <span v-text="`General Setting`"></span>
      </template>
    </page-header>

    <general-skeleton v-if="setting.isFetching"></general-skeleton>

    <v-row v-else>
      <v-col cols="12" lg="6" class="mx-auto">
        <v-form ref="form" @submit.prevent="submit" enctype="multipart/form-data" :disabled="setting.isFetching">
          <v-card elevation="0">
            <v-card-title v-text="`App Information`"></v-card-title>
            <v-card-text>
              <form-errors :errors="setting.errors"></form-errors>
              <v-text-field
                label="App name"
                name="app:name"
                outlined
                v-model="setting.data[ 'app:name' ]"
                @input="setting.prestine( false )"
              ></v-text-field>
              <v-text-field
                label="Description"
                name="app:description"
                outlined
                v-model="setting.data[ 'app:description' ]"
                @input="setting.prestine( false )"
              ></v-text-field>
              <v-textarea
                outlined
                name="app:keywords"
                label="Keywords"
                v-model="setting.data[ 'app:keywords' ]"
                @input="setting.prestine( false )"
              ></v-textarea>
            </v-card-text>
            <v-card-actions> 
              <v-btn color="primary" type="submit" :loading="setting.isFetching" :disabled="setting.isPrestine">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-form>
      </v-col>
      <v-col cols="12" lg="6">
        <image-uploader :src="logo" title="App Logo" :url="api.uploadLogo()" @updated="logoUpdated()"></image-uploader>
      </v-col>
    </v-row>
  </user>
</template>

<script>
import Api from './routes/api'
import Setting from './models/Setting'
import { mapActions, mapGetters } from 'vuex'

export default {
  components: {
    GeneralSkeleton: () => import('./cards/GeneralSkeleton')
  },

  computed: {
    ...mapGetters({
      auth: 'auth/auth',
    }),

    logo () {
      return this.setting.data[ 'app:logo' ] ? this.setting.data[ 'app:logo' ] : '/storage/images/placeholder.jpeg'
    }
  },

  data: () => ({
    api: Api,
    setting: new Setting,
  }),

  methods: {
    ...mapActions({
      setSnackbar: 'snackbar/set',
      initSetting: 'setting/init',
    }),

    async submit () {
      const data = await this.setting.request({
        method: 'post',
        url: Api.editAll(),
        data: this.setting.readyData( this.$refs.form.$el ),
      })
      if( data ) {
        this.setSnackbar({
          model: true,
          text: 'Settings updated',
          timeout: 3000
        })
        this.initSetting()
      }
    },

    logoUpdated () {
      this.initSetting()
    }
  },

  mounted () {
    this.setting.getResource({
      url: Api.getAll(),
      params: {
        keys: [ 'app:name', 'app:description', 'app:keywords', 'app:logo' ]
      }
    })
    this.setting.load( false )
  }
}
</script>